@use 'base/variables' as var;
@use 'base/mixins' as m;

.main-nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5rem;
    @include m.tablet {
        flex-direction: row;
        gap: 1rem;
        margin-top: 0;
    }
    a {
        color: var.$white;
        font-size: 2rem;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 1rem;
        @include m.tablet {
            margin-bottom: 0;
        }
        &:hover {
            color: var.$secondary;
        }
        &.active {
            color: var.$secondary;
        }
    }
}