@use 'variables' as var;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: var.$secondaryFont;
    font-size: 1.6rem;
    background-color: var.$lightBrown;
}
p {
    color: var.$black;
    font-size: 1.6rem;
    line-height: 2;
}
.container {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
    color: var.$black;
}
img {
    max-width: 100%;
    height: auto;
    display: block;
}
ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
